@each $name, $value in $theme-colors {
    .text_{
        &_#{$name} {
            color: $value !important;
        }
    }
}

@each $level, $value in $heading-font-sizes {
    #{$level}, .#{$level} {
        font-size: $value;
        font-family: $font-family-headline;
        line-height: $headline-line-height;
        font-weight: $headline-font-weight;
    }
}

@each $level, $value in $display-font-size {
    .display_{
        &_#{$level} {
            font-size: $value;
            line-height: $display-line-height;
            font-weight: $display-font-weight;
            font-family: $font-family-display;
            text-transform: uppercase;
        }
    }
}

a {
    font-weight: $link-weight;
    text-decoration: $link-text-decoration;
    font-style: $link-style;
    color: inherit;
    transition: all .3s ease-in-out;
    &:hover {
        font-style: $link-style-hover;
        text-decoration: $link-text-decoration-hover;
        opacity: $link-opacity-hover;
    }
}

.subtitle {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: $font-family-base;
    font-size: 12px;
}

.chip {
    padding-top: $spacer-1 * .25;
    padding-bottom: $spacer-1 * .25;
    padding-left: $spacer-1;
    padding-right: $spacer-1;
    border-radius: $br-pill;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: inline-flex;
    width: auto;
    margin-right: $spacer-1 * .25;
    margin-bottom: $spacer-1 * .25;
    font-weight: 400;

    @each $name, $value in $theme-colors {
        &.bg_{
            &_#{$name} {
                background-color: darken($value, 10%) !important;
                @if $name != 'light' {
                    color: $light;
                }
            }
        }
    }

}

.lead {
    font-size: $lead-font-size;
    font-weight: $lead-font-weight;
}

.list-unstyled {
    list-style: none;
    padding: 0;
}

.icon__list {
    padding: 0;
    list-style: none;

    li {
        position: relative;
        padding-left: $spacer-4;
        margin-bottom: $spacer-2;
        &:before {
            content: '—';
            color: $secondary;
            position: absolute;
            top: 0;
            left: 0;
        }
    }

}