nav.nav__bar {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 10000000;
    background-color: $dark;
    padding: $spacer-1 $spacer-3;
    transition: all .3s ease-in-out;

    &.active {
        background-color: transparent;
    }

    .nav__brand {
        margin-left: auto;
        margin-right: auto;
        transform: translateX(50%);

        @media screen and (max-width: $breakpoint-md) {
            margin-left: 0;
            transform: translateX(0%);
        }

        a {
            opacity: 1;

            &:hover {
                opacity: 1;
            }
        }

        svg {
            display: block;
            height: 40px;
            fill: $secondary;

            &:hover {
                fill: $secondary;
            }

        }
    }

    .nav__toggle {

        #menu__toggle {
            height: 50px;
            width: 50px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            transform: translateY(-10px);
            opacity: 1;
            border: 0;
            background-color: transparent;
        }

        .toggle__bar {
            display: block;
            height: 1px;
            width: 30px;
            background-color: $light;
            transform: translateY(1rem);
            position: relative;
            transition: all .3s ease-in-out;
            
            &:after {
                content: '';
                display: block;
                height: 1px;
                width: 35px;
                background-color: $light;
                position: absolute;
                bottom: .5rem;
                transition: all .3s ease-in-out;
            }

            &.active {

                transform: translateY(.73rem) rotate(45deg);
                transform-origin: center;
                width: 35px;

                &:after{
                    transform: translateY(.25rem) rotate(90deg);
                    transform-origin: center;
                    bottom: 3px;
                }

            }

        }
    }

}

.nav__panel {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: $dark;
    z-index: 100;
    transform: translateX(110vw);
    transition: .4s ease-in-out;

    .nav__item {
        transform: translateY(-50%);
        opacity: 0;
        margin-bottom: 0;       
    }

    .nav__link {
        font-family: $font-family-display;
        font-size: calc(1.575rem + 3.9vw);
        color: $light;
        font-weight: 400;
        text-transform: uppercase;
        margin-bottom: 0;
    }

    .nav__link-panel {
        height: 100vh;
        padding: $spacer-3;
        display: flex;
        align-items: center;
    }

    .nav__bg-image {
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }


    &.active {
        transform: translateX(0);

        .nav__item {
            transform: translateY(0);
            opacity: 1;
            transition: .4s ease-in-out;
        }

    }

}