.image__rounded {
    overflow: hidden;
    border-radius: $br-md;
    display: block;

    > img {
        display: block;
        width: 100%;
        height: auto;
        object-fit: cover;
    }

}

.image__fullwidth {
    @extend .br__md;
    overflow: hidden;
    
    img {
        width: 100%;
        height: auto;
        object-fit: cover;
    }
}

.cs-2-col-image, .cs-fullwidth-image {
    figure {
        @extend .br__md;
        display: block;
        height: 100%;
        overflow: hidden;
        background-color: white;

        img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }

    }
}