footer {
    .limiter {
        background-color: darken($light, 10%);

        &.subfooter {
            background-color: transparent;

            p {
                font-size: $btn-font-size;
                margin-bottom: 0;
                text-transform: uppercase;
            }
        }

        .btn {
            @extend .btn__primary;
        }

    }
}

.bg__{
    &dark {
        footer {
            .limiter {
                background-color: $primary;

                &.subfooter {
                    background-color: transparent;

                    p {
                        color: $light;
                    }

                }

                .display__1, h4 {
                    color: $light;
                }

                .btn {
                    @extend .btn__secondary;
                }

            }
        }
    }
}

.bg__, .bg__light {
    footer {
        .limiter {
            background-color: darken($light, 10%);

            &.subfooter {
                background-color: transparent;
            }

            .btn {
                @extend .btn__primary;
            }

        }
    }
}


.client__block {

    hr {
        border: 0;
        border-bottom: 1px solid;
        border-color: $light;
    }

    .client__container {
        background-color: darken($light, 10%);

        .col-lg {
            padding: $spacer-2;

            svg {
                display: block;
                max-width: 150px;
                fill: $primary;
                margin-left: auto;
                margin-right: auto;
            }

        }

    }

}

body.bg__dark {

    .client__block {

        hr {
            border-color: lighten($primary, 10%);
        }

        h2, p {
            color: $light;
        }
    
        .client__container {
            background-color: $primary;
    
            .col-lg {
                svg {
                    fill: $light;
                }
    
            }
    
        }
    
    }

}