@each $selector-abbv, $property in $grid-pm {
    @each $axis-abbv, $axis in $axis {
        @each $level, $value in $spacing-units {
            @if ($axis-abbv == t) or ($axis-abbv == b) {
                .#{$selector-abbv}#{$axis-abbv}__#{$level}, .#{$selector-abbv}y__#{$level} {
                    #{$property}-#{$axis}: $value;
                }
            }@else {
                .#{$selector-abbv}#{$axis-abbv}__#{$level}, .#{$selector-abbv}x__#{$level} {
                    #{$property}-#{$axis}: $value;
                }
            }
        }
    }
}

.ml__auto, .mx__auto {
    margin-left: auto;
}

.mr__auto, .mx__auto {
    margin-right: auto;
}

.mt__auto, .my__auto {
    margin-top: auto;
}

.mb__auto, .my__auto {
    margin-bottom: auto;
}

@each $level, $value in $heights-viewport {
    .vh_{
        &_#{$level} {
            min-height: $value;
            height: 100%;
        }
    }
}

.limiter {
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    flex: 0 0 auto;
    width: 91.66666667%;
}

.panel {
    @extend .py__2;
    @extend .px__2;
    @extend .br__md;
    overflow: hidden;
}

